<template>
  <div class="home" v-title="'高级活动配置中心'">
    <div class="blk" :key="index" v-for="(item, index) in fliterList">
      <h3>{{config[item.title]}}配置</h3>
      <div class="config-list">
        <v-button size="xs" inline type="primary" :key="i" v-for="(file, i) in item.files" :href="`#/detail/${item.title}?file=${file}`">{{file}}</v-button>
      </div>
      <div class="options">
        <v-button size="xs" inline type="success" :href="`#/detail/${item.title}`">新增</v-button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { getConfigList } from '../api'
export default {
  name: 'home',
  data () {
    return {
      list: [],
      config: {
        above: '满赠'
      }
    }
  },
  computed: {
    fliterList () {
      return this.list.map(({title, files}) => {
        return {title, files: files.filter(file => file.indexOf('template') === -1)}
      })
    }
  },
  created () {
    getConfigList()
    .then(data => this.list = data)
  }
}
</script>
<style lang="scss" scoped>
.home {
  .blk  {
    background: #fff;
    border: 1px solid #eee;
    border-radius: 10px;
    padding: 20px;
    .config-list {
      padding: 20px 0;
    }
    .options {
      padding-top: 20px;
      border-top: 1px solid #eee;
      text-align: right;
    }
  }
}
</style>
