import request, {
  busyRequest
} from '@/utils/request'

const busyTest = busyRequest()

export function test(data) { // get
  return request('/mock/test', {
    params: data
  })
}

export function getConfigList(params) { // get
  return request('/api/getConfigList', {
    params
  })
}
export function getConfig(params) { // get
  return request('/api/getConfig', {
    params
  })
}
export function saveConfig(data) { // post
  return request('/api/saveConfig', {
    method: 'POST',
    data
  })
}

export function busyTestReq(data) { // post
  return busyTest('/test', {
    methods: 'POST',
    headers: {},
    data,
  })
}
