const verifyRule = {
  mobile(val) {
    return /^1[3|4|5|6|7|8|9][0-9]{9}$/.test(val);
  }
};

const formatList = {
  thousand(val) {
    return val.replace(/(^|\s)\d+/g, s =>
      s.replace(/(?=(?!\b)(\d{3})+$)/g, ',')
    );
  },
  date(date, c = '-') {
    const d = new Date(date);
    return `${d.getFullYear()}${c}${this.zero(d.getMonth() + 1)}${c}${this.zero(
      d.getDate()
    )}`;
  },
  datetime(date) {
    const d = new Date(date);
    return `${this.date(d)} ${this.zero(d.getHours())}:${this.zero(
      d.getMinutes()
    )}:${this.zero(d.getSeconds())}`;
  },
  zero(s, n = 2) {
    return s ? String(s).padStart(n, '0') : '00';
  }
};

export function pasteText (text) { // 复制文本
  let msg = ''
  
  const textarea = document.createElement('textarea')
  textarea.style.position = 'fixed'
  textarea.style.top = 0
  textarea.style.left = 0
  textarea.style.border = 'none'
  textarea.style.outline = 'none'
  textarea.style.resize = 'none'
  textarea.style.background = 'transparent'
  textarea.style.color = 'transparent'

  textarea.value = text
  document.body.appendChild(textarea)
  textarea.select()

  try {
    msg = document.execCommand('copy') ? '复制成功' : '复制失败'
  } catch (err) {
    msg = err
  }
  document.body.removeChild(textarea)
  return msg
}

export function verify(type, val) {
  return typeof verifyRule[type] === 'function' && verifyRule[type](val);
}

export function format(type, val) {
  return typeof formatList[type] === 'function' && formatList[type](val);
}

export const isDev = process.env.NODE_ENV === 'development';

const ua = navigator.userAgent;

export const platform = navigator.platform;
export const isIOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
export const isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Linux') > -1;
export const isWeixin = !!ua.match(/MicroMessenger/i);

export default {
  install(Vue) {
    Vue.prototype.$base = {};

    const doc = document.documentElement; // 获取屏幕尺寸
    const baseWidth = 750; // 设计图基准尺寸
    const baseFontSize = 100; // 基准字体大小

    const getSize = () => {
      doc.style.fontSize = Math.min((doc.clientWidth / baseWidth) * baseFontSize, 60) + 'px';
      Vue.prototype.$base.clientWidth = doc.clientWidth;
      Vue.prototype.$base.clientHeight = doc.clientHeight;
      Vue.prototype.$base.pageHeight = doc.offsetHeight;
    };

    getSize();

    window.onresize = () => {
      getSize();
    };

    Vue.prototype.$bus =
      Vue.prototype.$bus ||
      new Vue({
        data: {}
      });

    // 自定义title 用法 如<div v-title="'title'"></div>
    Vue.directive('title', {
      inserted(el, binding) {
        document.title = binding.value;
      },
      update(el, binding) {
        document.title = binding.value;
      }
    });

    let scorllVal = null;
    const scroll = e => {
      e.preventDefault();
      Vue.prototype.$nextTick(() => {
        let posy = isNaN(+scorllVal) ? getPos(scorllVal) : +scorllVal;
        let now = window.scrollY;
        let step = (now - posy) / 15;
        if (step) {
          let timer = setInterval(() => {
            now -= step;
            let isOver = step > 0 ? now <= posy : now >= posy;
            if (isOver) {
              now = posy;
              clearInterval(timer);
            }
            window.scrollTo(0, now);
          }, 17);
        }
      })
    };
    const getPos = (id) => {
      const el = document.getElementById(id)
      return el ? el.offsetTop : 0
    }
    Vue.directive('scroll', {
      bind(el, binding) {
        scorllVal = binding.value
        el.addEventListener('click', scroll, false)
      },
      update(el, binding) {
        scorllVal = binding.value;
      },
      unbind(el) {
        scorllVal = null;
        el.removeEventListener('click', scroll)
      }
    });
  }
};
