<template>
  <div class="detail" v-title="'高级活动配置中心'">
    {{typeof detail}}
    <ul>
      <li :key="key" v-for="(value, key) in detail">
        <v-input :label="key" size="xs" v-model="detail[key]"></v-input>
      </li>
    </ul>
    <div class="float">
      <v-button inline size="xs" type="primary" @click="saveName">保存</v-button>
      <v-button inline size="xs" type="success" @click="toJSON">生成json</v-button>
    </div>
    <v-alert ref="code" btnSize="xs" okTxt="复制" @ok="pasteJSON">
      <div class="alert-blk">
        <code>{{this.detail}}</code>
      </div>
    </v-alert>
    <v-alert ref="save" title="请输入一个配置名称" btnSize="xs" okTxt="确定" @ok="save">
      <div class="alert-blk">
        <v-input size="xs" v-model="name"></v-input>
      </div>
    </v-alert>
  </div>
</template>

<script>
// @ is an alias to /src
import { getConfig, saveConfig } from '../api'
import { pasteText } from 'utils/base'
export default {
  name: 'detail',
  data () {
    return {
      type: '',
      name: '',
      detail: {}
    }
  },
  created () {
    this.type = this.$route.params.type
    this.name = this.$route.query.file
    const file = this.$route.query.file ? this.$route.query.file : 'template'
    getConfig({path: encodeURIComponent(this.type + '/' + file)})
    .then(data => this.detail = JSON.parse(data))
  },
  methods: {
    toJSON () {
      this.$refs.code.open()
    },
    pasteJSON () {
      console.log(JSON.stringify(this.detail));
      const msg = pasteText(JSON.stringify(this.detail))
      this.$toast.open(msg)
    },
    saveName () {
      this.$route.query.file ? this.save() :
      this.$refs.save.open()
    },
    save () {
      saveConfig({path: `${this.type}/${this.name}`, config: this.detail})
      .then(() => this.$toast.open('保存成功'))
      .catch(err => this.$toast.open(err.msg))
    }
  }
}
</script>

<style lang="scss" scoped>
.detail {
  ul {
    background: #fff;
    padding: 20px;
  }
  .alert-blk {
    padding: 20px 20px 40px;
  }
}
</style>
